import Vue from 'vue';
import Router from 'vue-router';
import { isStandaloneApp } from '@/utils/standaloneApp';
import marketRoutes from './routes/market';
import standaloneAppRoutes from './routes/standaloneApp';

Vue.use(Router);

function getScrollBehavior() {
  try {
    const content = document.getElementById('content');
    const padding = parseFloat(window.getComputedStyle(content, null).getPropertyValue('padding-top'));

    if (content.getBoundingClientRect().top + padding < 0) {
      return { y: 0 };
    }
  } catch {
    // empty
  }

  return {};
}

function createRoutes(isStandalone) {
  if (isStandalone) {
    return standaloneAppRoutes;
  }

  return marketRoutes;
}

function createRouter() {
  const routes = createRoutes(isStandaloneApp(window.location.host));
  const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    scrollBehavior(to, from, savedPosition) {
      if (to.hash) {
        return { selector: to.hash };
      }

      if (savedPosition) {
        return savedPosition;
      }

      return getScrollBehavior();
    },
    routes,
  });

  router.beforeEach(async (to, from, next) => {
    Vue.store.commit('router/pushToHistory', from);

    if (!Vue.store.state.isDataFetched) {
      await Vue.store.dispatch('fetchData');
    }

    const toProfile = to.matched[0].path === '/my';
    const { authRequired } = to.meta;

    if (!Vue.store.getters.isDeviceLinked && toProfile && authRequired) {
      next({ path: '/my/error' });
    } else {
      next();
    }
  });

  return router;
}

export default createRouter;
